.survey-form-container {
    display: flex;
    flex-direction: column;
    height: 100vh;
  }
  
  .header-bar {
    background-color: #065f27; /* Blue color */
    text-align: center;
    width: 100vw;
  }
  
  .header-title {
    margin: 0;
    color: #fff;
  }
  
  .content {
    background-color: #fff;
    padding: 20px;
  }
  
  button {
    margin-top: 10px;
  }
  
  label {
    display: block;
    margin-bottom: 5px;
  }

  .question-container {
    background-color: #4fab65; /* Light green background */
    text-align: center;
    width: 30vw;
    border-radius: 10px; /* Rounded corners */
    padding: 10px;
    margin-bottom: 10px; /* Add some spacing between questions */
  }